html,
body,
#root,
.wrapper {
  margin: 0;
  width: 100%;
  height: 100%;
  color: #fff;
  font-size: 26px;
  font-family: 'Roboto', sans-serif;
  background: #f85859;
  overflow: hidden;
}
h1 {
  margin: 0;
}
@media screen and (max-width: 600px) {
  .wrapper {
    font-size: 19px;
  }
  .wrapper .info {
    font-size: 1em !important;
  }
}
.start-page {
  font-family: 'Vollkorn', serif;
  position: absolute;
  top: 50%;
  left: 50%;
  text-align: center;
  transform: translate(-50%, -50%);
}
.start-page h1 {
  font-size: 2em;
  line-height: 1em;
  margin-bottom: 0.5em;
}
.game-page {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}
.game-page .info {
  display: flex;
  font-size: 0.8em;
  justify-content: space-between;
  width: 50vh;
  position: absolute;
  top: 15px;
}
.game-page .board {
  width: 50vh;
  height: 50vh;
  background: #fff;
  border-radius: 5px;
  display: grid;
  grid-column-gap: 5px;
  grid-row-gap: 5px;
  padding: 5px;
}
.game-page .board.x1 {
  grid-template-columns: repeat(1, 1fr);
}
.game-page .board.x2 {
  grid-template-columns: repeat(2, 1fr);
}
.game-page .board.x3 {
  grid-template-columns: repeat(3, 1fr);
}
.game-page .board.x4 {
  grid-template-columns: repeat(4, 1fr);
}
.game-page .board.x5 {
  grid-template-columns: repeat(5, 1fr);
}
.game-page .board.x6 {
  grid-template-columns: repeat(6, 1fr);
}
.game-page .board.x7 {
  grid-template-columns: repeat(7, 1fr);
}
.game-page .board.x8 {
  grid-template-columns: repeat(8, 1fr);
}
.game-page .board.x9 {
  grid-template-columns: repeat(9, 1fr);
}
.game-page .board.x10 {
  grid-template-columns: repeat(10, 1fr);
}
.game-page .board.x11 {
  grid-template-columns: repeat(11, 1fr);
}
.game-page .board.x12 {
  grid-template-columns: repeat(12, 1fr);
}
.game-page .board.x13 {
  grid-template-columns: repeat(13, 1fr);
}
.game-page .board.x14 {
  grid-template-columns: repeat(14, 1fr);
}
.game-page .board.x15 {
  grid-template-columns: repeat(15, 1fr);
}
.game-page .board .cell {
  border-radius: 5px;
  background: red;
}
.btn {
  height: 52px;
  width: 100%;
  padding: 12px;
  border: solid 2px #fff;
  color: #fff;
  text-transform: uppercase;
  font-size: 1em;
  line-height: 1em;
  border-radius: 3px;
  background: #f85859;
  outline: none !important;
  cursor: pointer;
}
.btn:hover {
  opacity: 0.9;
}
